$primary: #0052e9;
$secondary: #ffab00;
$black10: rgba(0, 0, 0, 0.1);

.color-primary {
  color: $primary;
}

.color-secondary {
  color: $secondary;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Roboto', sans-serif;
  color: #3a3f52;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @media (min-width: 992px) {
    padding-top: 83px;
  }

  @media (max-width: 991px) {
    padding-top: 59px;
  }
}

strong {
  font-weight: 600;
}

h1 {
  font-size: 1.5rem;

  @media (min-width: 992px) {
    font-size: 2.5rem;
  }
}

h2 {
  font-size: 1.3rem;

  @media (min-width: 992px) {
    font-size: 2rem;
  }
}

a {
  &.text-link {
    color: rgba(0, 0, 0, 0.83);
    text-decoration: underline;
    margin: 0 2px 0 4px;
  }

  &:hover {
    text-decoration: none;
  }
}

p,
li {
  font-weight: 300;
  color: rgba(0, 0, 0, 0.83);

  @media(min-width: 992px) {
    letter-spacing: .05em;
  }
}

ul {
  @media(max-width: 991px) {
    padding-left: 20px;
  }

  li {
    font-size: 15px;
    line-height: 20px;
    margin-bottom: 10px;
  }
}

hr {
  margin: 32px 0;
}

.crop {
  width: 100%;
  margin-bottom: 20px;
  overflow: hidden;
  text-align: center;
  display: inline-block;
  position: relative;

  &:after {
    padding-top: 56.25%;
    display: block;
    content: '';
  }

  img {
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
}

/* ==============================================
     2. Navbar 
  ===============================================*/

.navbar {
  background-color: white; //box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);

  @media (min-width: 992px) {
    padding: 20px;
  }

  .navbar-brand {
    padding-left: 0;
  }

  .logo-img {
    max-height: 48px;
    border: 3px solid white;
    border-radius: 50%;
    margin: -4px 16px 0 0;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .07);
  }

  .logo-text {
    display: inline-block;
    letter-spacing: 1px;
    margin: 0;
    font-size: 1.3rem;
    font-weight: 700;
  }

  .nav-link {
    font-size: 15px;
    font-weight: 400;
    letter-spacing: .05em;
    padding: 10px 16px;
    display: block;
    color: black;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  .navbar-toggler {
    padding: .25rem;
    border: none;
  }

  /* Large devices (desktops, 992px and up) */
  @media (min-width: 992px) {
    &.navbar-expand-lg .navbar-nav .nav-link {
      padding-left: 40px;
    }
  }

  @media (max-width: 991px) {
    .logo-text {
      font-size: 1.1rem;
      letter-spacing: 0;
    }

    .navbar-collapse::after {
      content: "";
      position: fixed;
      top: 266px;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.4);
    }
  }
}


