/* ==============================================
     4. About
  ===============================================*/

#about {
  h1 {
    margin-bottom: 20px;
  }

  .hero {
    padding: 0;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; //background-attachment: fixed;
    //height: 50vh;
  }

  .title {
    margin-bottom: 30px;
  }

  .portrait {
    width: 66%;
    border-radius: 50%;
    border: 8px solid white;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .07);
  }

  // p:last-child{
  //   margin-bottom: 0;
  // }
  .instagram {
    margin-top: 50px;
    border-top: 1px solid $black10;
    padding-top: 50px;
  }

  .instagram-photo {
    border: 1px solid $black10;
    border-radius: 2px;
    display: block;

    img {
      width: 100%;
      border: 6px solid white;
    }
  }
}

/* ==============================================
     5. Home
  ===============================================*/

#home {
  .hero {
    position: relative;
    padding: 30vh 0;
    h2 {
      font-size: 50px;
      font-weight: 300;
      margin-bottom: 20px;
    }

    h3 {
      font-weight: 600;
      margin-bottom: 20px;
    }

    p {
      font-size: 24px;
      font-weight: 300;
      margin-bottom: 0px;
      padding: 0px;
    }
    @media (min-width: 992px){
      margin-bottom: 60px;
    }
    @media (max-width: 991px) {
      padding: 15vh 0;

      h2 {
        font-size: 24px;
        display: none;
      }

      h3 {
        font-size: 1rem;
      }

      p {
        font-size: 1rem;
      }
    }
  }
  .particles-layout{
    background: #0052e9;
  }
}

.crop {
  width: 100%;
  margin-bottom: 20px;
  overflow: hidden;
  text-align: center;
  display: inline-block;
  position: relative;

  &:after {
    padding-top: 56.25%;
    display: block;
    content: '';
  }

  img {
    top: 50%;
    height: auto;
    width: 100%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}

/* ==============================================
     6. Portfolio
  ===============================================*/

#portfolio {
  section {
    @media (min-width: 992px) {
      margin: 100px 0px;
    }
  
    @media (max-width: 991px) {
      margin: 30px 0;
    }
  }
  h3 {
    font-size: 1.4em;
    font-weight: 500;

    &:not(:first-child) {
      margin-top: 50px;
    }
  }

  h4 {
    font-size: 1.1em;
    margin-bottom: 20px;

    &:not(:first-child) {
      margin-top: 50px;
    }
  }

  h5 {
    margin-top: 30px;
  }

  .hero {
    padding: 0;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; // background-attachment: fixed;
    height: 50vh;
  }

  .title-block {
    margin-bottom: 50px;
    padding-bottom: 50px;

    @media(max-widh: 991px) {
      margin-bottom: 10px;
    }
  }

  .img-fluid {
    margin: 30px 0;
  }

  .card {
    opacity: 0.8;
    transition: 0.3s all;
    border: none;
    margin-bottom: 40px;
    &:hover {
      transition: 0.3s all;
      opacity: 1;
      box-shadow: 0px 22px 43px rgba(68, 68, 68, 0.2);

      .card-body {
        transition: 0.3s all;
        padding-left: 1.25rem;
      }
    }

    .card-body {
      transition: 0.3s all;
      position: relative;
      padding-left: 0;
    }

    .card-title {
      color: rgba(0, 0, 0, 0.87);
      font-weight: 700;
      display: inline-block;
    }

    .card-title:last-child {
      margin-bottom: 0;
    }

    @media (min-width: 992px) {
      .card-title {
        font-size: 28px;
        letter-spacing: -0.5px;
      }
    }

    @media (max-width: 991px) {}
  }
}

